@import 'core/sass/variables';

.issue {
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  cursor: pointer;

  .priority {
    text-align: left;
  }

  .title {
      margin-left: 10px;
      text-align: left;
    }

  .btn {
    color: $gray;
    margin-left: auto;
    margin-right: 10px;
      &:hover {
        color: red;
        cursor: pointer;
      }
  }
}

.green {
  background-color: $green-light;
}
